import { insertBefore } from "AC/helpers/elements";
export var initTaxonomyFilters = function (taxonomyFilters) {
    if (taxonomyFilters) {
        var bulkActions = document.querySelector('.tablenav.top .bulkactions');
        var button_1 = document.querySelector('#wpfooter input[name=acp_filter_action]');
        bulkActions.parentNode.insertBefore(taxonomyFilters, bulkActions);
        if (button_1) {
            taxonomyFilters.append(button_1);
            button_1.addEventListener('click', function () {
                var form = button_1.closest('form');
                form.method = 'GET';
                form.dispatchEvent(new Event('submit'));
            });
        }
    }
};
export var initUserFilterButton = function (FilterButton) {
    document.querySelectorAll('.acp-range input').forEach(function (input) {
        input.addEventListener('keydown', function (e) {
            if (e.key === 'Enter') {
                FilterButton.dispatchEvent(new Event('click'));
            }
        });
    });
};
export var createMsFilterForm = function () {
    var foundForm = document.querySelector('#ac_form-user-list');
    if (foundForm) {
        return foundForm;
    }
    var form = document.createElement('form');
    form.id = 'ac_form-user-list';
    form.method = 'get';
    insertBefore(form, document.querySelector('#form-user-list'));
    return form;
};
export var placeMsUserFilterMarkup = function () {
    var form = createMsFilterForm();
    document.querySelectorAll('select.acp-filter, input[name=acp_filter_action]').forEach(function (el) { return form.append(el); });
};
