import ACQueryBuilder from "./modules/ac-query-builder";
import Segments from "./modules/segments";
import WhitelistedUrlParams from "./modules/segments/whitelisted-url-params";
import ResetFilters from "./modules/reset-filters";
import { ActiveFilters } from "./modules/active-filters";
import { getConfiguredFilterTypes } from "./types/functions";
import { initJqueryDateYearScroller } from "./plugin/jquery-date-year-scroller";
import { initAcServices } from "AC/helpers/admin-columns";
initJqueryDateYearScroller();
document.addEventListener('DOMContentLoaded', function () {
    var AcServices = initAcServices();
    AcServices.registerService('ActiveFilters', new ActiveFilters());
    AcServices.registerService('ResetFilters', new ResetFilters(AcServices.getService('ActiveFilters')));
    document.querySelectorAll('[name="acp_filter_action"], #post-query-submit').forEach(function (el) { return el.classList.add('ac-search-button'); });
    if (AC.layout && document.getElementById('ac-s') && ac_search.filters.length) {
        var SearchModule = new ACQueryBuilder(document.getElementById('ac-s'), ac_search, getConfiguredFilterTypes(), AcServices);
        AcServices.registerService('Search', SearchModule);
        AcServices.emitEvent('Search.Loaded', SearchModule);
        SearchModule.init();
        if (document.querySelector('.ac-segments')) {
            var SegmentsModule = new Segments(AcServices.getService('Table'), SearchModule, new WhitelistedUrlParams(document.querySelector('#ac-s')));
            AcServices.registerService('Segments', SegmentsModule);
        }
        refreshFilters();
    }
});
document.addEventListener('AC_Search_State_Change', function () { return refreshFilters(); });
var refreshFilters = function () {
    initAcServices().getService('ActiveFilters').refresh();
};
